.section_card_2 {
	padding-bottom: rem(140);

	@include min768 {
		padding-bottom: rem(100);
	}

	.section_header {
		margin-bottom: rem(40);

		@include min768 {
			margin-bottom: rem(26);

			.title-h2 {
				br {
					display: none;
				}
			}
		}
	}

	&__row {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min1050 {
			flex-direction: column;
		}

		.image {
			flex: 0 0 rem(500);
			max-width: rem(500);
			margin-right: rem(90);

			height: rem(630);

			@include min1050 {
				margin-right: 0;
				flex: 0 0 100%;
				max-width: 100%;
			}

			@include min768 {
				flex: 0 0 rem(315);
				height: rem(315);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.slider {
			margin-top: rem(45);
			flex: 1;
			min-width: 0;

			@include min1050 {
				flex: 0 0 100%;
				max-width: 100%;
			}

			&_right__desktop {
				@include min768 {
					display: none;
				}

				
			}

			&_right__mobile {
				display: none;

				@include min768 {
					display: block;
				}
			}

			.item {
				display: flex;
				margin-bottom: rem(67);

				&:last-child {
					margin-bottom: 0;
				}

				svg {
					flex: 0 0 rem(50);
					width: rem(50) !important;
				}

				&__text {
					margin-left: rem(44);

					@include min768 {
						margin-left: rem(24);
					}

					p {
						font-size: rem(18);
						line-height: 150%;
						color: var(--c-text);

						@include min1050 {
							br {
								display: none;
							}
						}

						@include min540 {
							font-size: rem(14);
						}
					}
				}
			}

			&__nav {
				display: flex;
				align-items: center;
				margin-top: rem(72);

				@include min1280 {
					width: 70%;
				}

				@include min768 {
					width: 100%;
					margin-top: rem(52);
				}

				.nav {
					display: flex;
					align-items: center;

					&_prev {
						margin-right: rem(16);

						@include min768 {
							margin-right: rem(8);
						}
					}

					&_prev, &_next {
						width: rem(72);
						height: rem(72);
						border-radius: 50%;
						border: rem(1) solid var(--c-green);

						display: flex;
						align-items: center;
						justify-content: center;

						@include min768 {
							width: rem(47);
							height: rem(47);
						}

					}

					.swiper-button-disabled {
						opacity: .25;
					}
				}

				.nav__line {
					margin: 0 rem(43);
					width: 100%;
					height: rem(1);
					background-color: #E7E4E4;

					@include min768 {
						margin: 0 rem(27);
					}
				}

				.nav__count {
					display: flex;
					align-items: flex-end;

					span {
						font-family: var(--f-prata);
						// line-height: normal;
					}

					.first {
						font-size: rem(48);
						line-height: rem(42);
						color: var(--c-green);
					}

					.last {
						&::before {
							content: "/";
							margin-left: rem(2);
							margin-right: rem(2);
						}
						font-size: rem(16);
						color: #B5B5B5;
					}
				}
			}
		}
		
	}

}