.info_two {
	padding-top: rem(122);
	padding-bottom: rem(112);

	@include min768 {
		padding-top: rem(90);
		padding-bottom: rem(90);
	}

	.section_header {
		margin-bottom: rem(76);

		@include min768 {
			margin-bottom: rem(48);
		}
	}

	.subtitle {
		font-size: rem(21);
		font-weight: 600;

		@include min768 {
			font-size: rem(15);
		}
	}

	.list {
		margin-top: rem(58);
		// margin-bottom: rem(110);
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min768 {
			margin-top: rem(28);
		}

		li {
			display: flex;
			flex-wrap: wrap;
			margin-right: rem(54);
			flex: 0 0 calc((100% - 108px) / 3);

			@include min1050 {
				margin-right: 0;
				flex: 0 0 100%;
				margin-bottom: rem(30);
			}

			&:last-child {
				margin-right: 0;

				@include min1050 {
					margin-bottom: 0;
				}
			}
		}

		.image {
			margin-right: rem(32);
			flex: 0 0 rem(47);
		}

		.text-p {
			flex: 1;
		}
	}

	.button_green {
		display: none;

		@include min768 {
			display: flex;
			margin-top: rem(49);
			margin-left: auto;
			margin-right: auto;
		}
	}

	.image_smart {
		display: none;

		@include min768 {
			display: block;
		}
	}
}

.home_map {
	position: relative;
	height: rem(795);

	@include min540 {
		height: rem(277);
	}

	&__image {
		height: 100%;
		
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__control {
		position: absolute;
		top: 0;
		left: 0;
		height: rem(795);
		width: 100%;

		display: flex;
		justify-content: center;

		@include min540 {
			height: rem(277);
		}

		.container {
			position: relative;
			width: 100%;
		}

		.item {
			position: absolute;

			&__content {
				position: absolute;
				bottom: rem(20);
				left: 0;
				margin-left: rem(-70);
				width: rem(347);
				// height: rem(179);
				background-color: var(--c-green);

				padding: rem(20) rem(30);

				display: flex;
				flex-direction: column;

				visibility: hidden;

				@include min540 {
					width: rem(214);
					padding: rem(12) rem(16);
				}

				b {
					font-size: rem(18);
					color: white;
					margin-bottom: rem(10);

					@include min540 {
						font-size: rem(10);
					}
				}

				p {
					font-size: rem(16);
					line-height: 130%;
					color: white;

					@include min540 {
						font-size: rem(10);
					}
				}
			}

			&__button {
				display: flex;
				align-items: center;

				> span {
					font-weight: bold;
					font-size: rem(18);
					color: white;
					margin-right: rem(14);

					@include min540 {
						display: none;
					}
					
				}
				

				.icon {
					position: relative;
					display: flex;

					.minus {
						display: none;
					}

					span {
						position: absolute;
						top: 50%;
						left: 50%;
						margin-top: rem(-14);
						margin-left: rem(-7);
						color: #F9FBFC;
						font-style: normal;
						font-weight: 300;
						font-size: rem(21);
					}

					.minus {
						margin-left: rem(-5);
					}
				}
			}
		}

		.item.active {
			.item__content {
				visibility: visible;
			}

			.item__button {
				> span {
					opacity: 0;
					pointer-events: none;
				}

				.icon {
					.plus {
						display: none;
					}
					.minus {
						display: block;

						@include min540 {
							color: var(--c-green);
						}
					}
				}
			}
			@include min540 {
				svg {
					circle {
						fill: white !important;
						stroke: var(--c-green) !important;
					}
				}
			}
			
		}

		.item1 {
			left: 66%;
			bottom: 57%;

			@include min540 {
				bottom: 64%;
			}
		}

		.item1.active {
			@include min540 {
				.item__content {
					margin-left: rem(-100);
				}
			}
		}

		.item2 {
			left: 12%;
			bottom: 42%;

			@include min540 {
				left: 6%;
				bottom: 40%;
			}

			.item__content {
				margin-left: rem(-52);
			}
		}

		.item2.active {
			@include min540 {
				.item__content {
					margin-left: rem(-20);
				}
			}
		}

		.item3 {
			left: 67%;
			bottom: 17%;

			@include min540 {
				left: 73%;
			}

			.item__content {
				margin-left: rem(-32);
			}
		}

		.item3.active {
			@include min540 {
				.item__content {
					margin-left: rem(-120);
				}
			}
		}

		.item4 {
			left: 26%;
			bottom: 2%;

			.item__content {
				margin-left: rem(-68);
			}
		}

		.item4.active {
			@include min540 {
				.item__content {
					margin-left: rem(-60);
				}
			}
		}
	}
}


.sect_message {
	background-color: white;
	padding-top: rem(127);
	padding-bottom: rem(105);

	@include min768 {
		padding-top: rem(80);
		padding-bottom: rem(80);
	}

	.section_header {
		margin-bottom: rem(28);

		@include min768 {
			margin-bottom: rem(22);
		}
	}

	.text-p {
		margin-bottom: rem(64);

		@include min768 {
			margin-bottom: rem(52);
		}
	}

	.form {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		// justify-content: space-between;

		.field-set {
			// flex: 0 0 rem(313);
			width: 100%;
			max-width: rem(313);
			margin-right: rem(40);

			@include min1050 {
				max-width: 100%;
				margin-right: 0;
				margin-bottom: rem(44);
			}
		}

		.field-button {
			flex: 1;
			width: 100%;
			max-width: rem(313);

			@include min1050 {
				max-width: 100%;
				justify-content: center;
			}

			.last {
				display: none;

				@include min768 {
					display: block;
				}
			}

			.first {
				@include min768 {
					display: none;
				}
			}

			.button_green {
				margin-bottom: rem(15);
				padding: 0 rem(40);

				@include min768 {
					margin-left: auto;
					margin-right: auto;
					width: rem(300);
				}

				&:hover {
					span {
						color: var(--c-green);
					}
				}
			}

			p {
				font-size: rem(16);
				line-height: 150%;
				color: rgba(72, 70, 66, .41);

				@include min768 {
					text-align: center;
					font-size: rem(12);
					max-width: rem(228);
					margin-left: auto;
					margin-right: auto;
				}

				a {
					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}
}