.info {
	position: relative;
	padding-top: rem(143);
	padding-bottom: rem(164);

	@include min1200 {
		display: flex;
		flex-direction: column;
	}

	@include min768 {
		padding-top: rem(53);
		padding-bottom: rem(70);
	}

	&::after {
		content: "";
		position: absolute;
		right: rem(-200);
		bottom: rem(-285);
		background: url(../img/section4/right_bg.png) no-repeat;
		width: rem(1263);
		height: rem(938);

		@include min1440 {
			right: rem(-280);
		}

		@include min1200 {
			// position: relative;
			// right: auto;
			// bottom: auto;
			// background: url(../img/section4/right_bg_smart.png) no-repeat;
			// width: 100%;
			// max-width: rem(750);
			// height: 100%;

			display: none;
		}
	}

	.container {
		@include min1200 {
			margin-left: 0;
			margin-right: 0;
		}
	}

	.image_smart {
		display: none;

		@include min1200 {
			display: block;

			width: 100%;
			height: 100%;
			object-fit: cover;

			min-height: rem(336);
		}
	}

	.section_header {
		margin-bottom: rem(28);

		@include min1200 {
			padding-left: 20px;
			padding-right: 20px;
		}

		.title-h2 {
			margin-bottom: rem(42);

			@include min768 {
				margin-bottom: 0;

				br {
					display: none;
				}
			}
		}

		svg {
			@include min768 {
				display: none;
			}
		}
	}

	.subtitle {
		font-weight: 600;
		margin-bottom: rem(36);
		max-width: rem(415);
		display: inline-block;
		line-height: 150%;

		@include min1200 {
			max-width: 100%;
			padding-left: 20px;
			padding-right: 20px;
		}

		@include min768 {
			font-size: rem(15);
		}
	}

	.list {
		display: flex;
		flex-direction: column;

		max-width: rem(495);

		margin-bottom: rem(63);

		@include min1200 {
			max-width: 100%;
			padding-left: 20px;
			padding-right: 20px;
		}

		li {
			margin-bottom: rem(38);

			display: flex;

			@include min768 {
				margin-bottom: rem(30);

				br {
					display: none;
				}
			}

			&:last-child {
				margin-bottom: 0;
			}

			.image {
				margin-right: rem(35);

				flex: 0 0 rem(47);
				max-width: rem(47);

				display: flex;
				justify-content: center;

				@include min768 {
					margin-right: rem(28);

					flex: 0 0 rem(36);
					max-width: rem(36);
				}
			}

		
		}
	}

	.button_green {
		&:hover {
			span {
				color: var(--c-green);
			}
		}

		@include min1200 {
			margin-left: auto;
			margin-right: auto;

			margin-bottom: rem(50);
		}
	}

	

}

.info__bottom {
	position: relative;
	// margin-top: rem(164);

	.section_header {
		margin-bottom: rem(28);

		@include min768 {
			margin-bottom: rem(26);
		}

		.title-h2 {
			margin-bottom: rem(26);

			@include min768 {
				margin-bottom: rem(16);
			}
		}
	}

	.text-p {
		padding-bottom: rem(68);
		// max-width: rem(502);

		@include min768 {
			padding-bottom: rem(32);
			max-width: 100%;

			br {
				display: none;
			}
		}
	}

	.items {
		display: flex;
		flex-wrap: wrap;

		margin-bottom: rem(57);

		.item {
			display: flex;
			flex-direction: column;

			margin-right: rem(75);

			@include min1200 {
				margin-right: rem(34);
			}

			

			@include min {
				flex: 0 0 calc((100% - 68px) / 2);
				max-width: calc((100% - 68px) / 2);

				&:first-child, &:nth-child(2) {
					margin-bottom: rem(40);
				}
			}

			@include min350 {
				margin-right: rem(24);

				flex: 0 0 calc((100% - 48px) / 2);
				max-width: calc((100% - 48px) / 2);
			}

			&:last-child {
				margin-right: 0;

				@include min1050 {
					display: none;
				}
			}

			&__top {
				color: var(--c-green);
				font-size: rem(18);
				margin-bottom: rem(7);
				font-family: var(--f-prata);

				

				span {
					font-size: rem(48);

				}
			}

			&__bottom {
				font-size: rem(18);
				font-family: var(--f-prata);

				@include min768 {
					font-size: rem(16);
				}
			}
		}
	}
}


.map {
	position: relative;
	height: rem(948);
	overflow-x: auto;

	@include min1200 {
		picture {
			position: absolute;
			top: 0;
			left: 0;
			width: 1200px;
		}
	}

	@include min540 {
		height: rem(378);
		overflow: hidden;

		picture {
			position: relative;
			top: 0;
			left: 0;
			width: 100%;
		}
	}

	img {
		width: 100%;
		height: rem(948);
		object-position: top;
		object-fit: cover;
		vertical-align: middle;

		@include min540 {
			height: 100%;

			width: 100%;
			object-position: top left;
		}

		@include min540 {
			object-position: -216px;
		}
	}


	.container {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		height: 100%;
	}

	&__item {
		position: absolute;
		
		display: flex;
		// flex-direction: column;
		align-items: center;

		// @include min768 {
		// 	top: rem(125);
		// 	left: rem(305);
		// }

		@include min540 {
			left: rem(90);
		}

		svg {
			@include min768 {
				width: rem(28);
				height: rem(35);
			}

			@include min540 {
				height: rem(30);
				position: absolute;
				top: 0;
				left: rem(-13);
			}
		}

		span {
			background-color: var(--c-green);

			padding: rem(15) rem(30);

			font-family: var(--f-prata);
			color: white;
			margin-left: rem(15);

			@include min768 {
				padding: rem(10) rem(20);
				font-size: rem(13);
			}

			@include min540 {
				margin-left: 0;
				padding: rem(6) rem(20);
				background-color: #CE7C50;
			}
		}
	}

	.item {
		cursor: pointer;
		position: absolute;

		transition: var(--transition);
		width: auto;

		// &:hover {
		// 	// width: rem(363);
		// 	z-index: 2;

		// 	// .item__text {
		// 	// 	display: block;
		// 	// }
		// 	.item__circl {
		// 		z-index: 2;
		// 	}
		// 	.item__content {
		// 		opacity: 1;
		// 		pointer-events: visible;
		// 	}
		// }

		&__circl {
			position: absolute;
			top: 50%;
			margin-top: rem(-8);
			left: rem(-9);
			width: rem(19);
			height: rem(19);
			background-color: white;
			border-radius: 50%;
			z-index: 2;
			border: rem(3) solid var(--c-green);

			display: flex;
			align-items: center;
			justify-content: center;

			svg {
				display: none;
			}

			@include min540 {
				width: rem(12);
				height: rem(12);
				background-color: var(--c-green);
				border: rem(2) solid white;
				top: rem(2);
				left: rem(2);
			}
		}

		&__title {
			font-size: rem(18);
			color: white;
			white-space: nowrap;
			font-family: var(--f-prata);

			@include min540 {
				font-size: rem(14);
			}
		}

		&__text {
			font-size: rem(16);
			color: white;
			line-height: 130%;
			margin-top: rem(6);

			display: none;
		}

		&__wrap {
			padding: rem(15) rem(20);
			background-color: var(--c-green);

			transition: var(--transition);

			@include min540 {
				background-color: #CE7C50;
				padding: rem(6) rem(20);
			}
		}

		&__content {
			position: absolute;
			top: 0;
			left: 0;
			// margin-top: -50%;
			transition: var(--transition);
			padding: rem(15) rem(20);
			background-color: var(--c-green);
			width: rem(363);
			pointer-events: none;

			@include min540 {
				width: rem(165);
				padding: rem(6) rem(20);
			}

			.title {
				font-size: rem(18);
				color: white;
				font-family: var(--f-prata);

				@include min540 {
					font-size: rem(14);
				}
			}
			.text {
				font-size: rem(16);
				color: white;
				line-height: 130%;
				margin-top: rem(6);

				@include min540 {
					font-size: rem(7);
				}
			}
		}
	}

	.item.active {
			// width: rem(363);
			z-index: 2;

			// .item__text {
			// 	display: block;
			// }
			.item__circl {
				z-index: 2;
				width: rem(18);
				height: rem(18);

				svg {
					display: block;
				}
			}
			.item__wrap {
				opacity: 0;
			}
			.item__content {
				opacity: 1;
				pointer-events: visible;
			}
	}

	.item1 {
		// top: 17%;
		top: rem(154);
		// left: 41.5%;
		left: rem(478);

		@include min540 {
			top: rem(50);
