			left: rem(158);
		}

		.item__circl {
			left: auto;
			right: rem(-9);

			@include min540 {
				top: auto;
				right: rem(-5);
				bottom: rem(-3);
			}
		}
		.item__wrap {
			padding: rem(15) rem(30) rem(15) rem(18);

			@include min540 {
				padding: rem(6) rem(18) rem(6) rem(20);
			}
		}
		.item__content {
			left: auto;
			right: 0;
			top: -72%;
			opacity: 0;
			padding: rem(15) rem(18) rem(15) rem(30);

			@include min540 {
				top: 0;
				padding: rem(16) rem(18) rem(16) rem(20);
			}
		}
	}

