		// top: 51%;
		top: rem(456);
		// left: 52%;
		left: rem(584);

		@include min540 {
			top: rem(207);
			left: rem(150);
		}

		.item__circl {
			// left: auto;
			left: rem(-9);
		}
		.item__wrap {
			padding: rem(15) rem(18) rem(15) rem(30);

			@include min540 {
				padding: rem(6) rem(18) rem(6) rem(20);
			}
		}
		.item__content {
			top: -72%;
			opacity: 0;
			padding: rem(15) rem(18) rem(15) rem(30);

			@include min540 {
				top: 0;
				padding: rem(16) rem(18) rem(16) rem(20);
			}
		}
	}

	.item5 {
		top: auto;
		// bottom: 6%;
		bottom: rem(87);
		// left: 56%;
		left: rem(647);

		@include min540 {
			top: rem(324);
			left: rem(182);
		}

		.item__circl {
			// left: auto;
			left: rem(-9);
		}
		.item__wrap {
			padding: rem(15) rem(18) rem(15) rem(30);

			@include min540 {
				padding: rem(6) rem(18) rem(6) rem(20);
			}
		}
		.item__content {
			top: -25%;
			opacity: 0;
			padding: rem(15) rem(18) rem(15) rem(30);

			@include min540 {
				top: 0;
				padding: rem(16) rem(18) rem(16) rem(20);
			}
		}
	}

}